import React from "react";
import { Typography, Paper } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";

const CompanyComponent = (props) => {
  const { company, classes, children } = props;
  return (
    <Paper className={classes.card}>
      <div className={classes.imageBackground}>
        {company.image && (
          <img
            src={company.image}
            alt={company.name}
            className={classes.image}
          />
        )}
        {company.me && (
          <Typography
            style={{ padding: "60px 200px", color: "#000" }}
            variant="h2"
          >
            About Me
          </Typography>
        )}
      </div>
      <div className={classes.textArea}>
        <Typography variant="h4" className={classes.content}>
          {company.name}
        </Typography>
        <Typography className={`${classes.content} ${classes.roleStyle}`}>
          {company.position} {company.timeline}
        </Typography>
        <Typography className={classes.content}>
          {company.technology}
        </Typography>
        <Typography className={classes.content}>
          {company.description}
        </Typography>
      </div>
      <div>
        {company.techImages.map((src, i) => (
          <a href={src.link || undefined}>
            <img key={i} src={src.image} alt="" className={classes.techImage} />
          </a>
        ))}
      </div>
      {children}
    </Paper>
  );
};

const styles = {
  image: {
    width: "400px",
    height: "200px",
    objectFit: "contain",
    backgroundColor: "#FFFFFF",
  },
  techImage: {
    width: "100px",
    height: "100px",
    objectFit: "contain",
    backgroundColor: "#FFFFFF",
    margin: "20px",
    borderRadius: "15%",
    border: "5px solid #08F",
  },
  content: {
    whiteSpace: "pre-wrap",
    textAlign: "initial",
  },
  roleStyle: {
    color: "#09f",
  },
  card: {
    margin: "20px",
    maxWidth: "1000px",
  },
  textArea: {
    padding: "20px",
  },
  imageBackground: {
    backgroundColor: "white",
  },
};

export const Company = withStyles(styles)(CompanyComponent);
